.navbar {
    /* background-color: #060b26 !important; */
    background-color: whitesmoke;
    height: 80px;
    display: flex;
    flex-direction: row;
    justify-content:start;
    padding: 0 8rem 0 7rem;
    align-items: flex-start;
  }
  .navbar img{
    width: 9rem;
    height: 4rem;
  

  }
  .menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
  }
  
  .nav-menu {
    /* background-color: #060b26; */
    background-color: whitesmoke;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    z-index:1;
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
  }
  
  .nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
  }
  
  .nav-text a {
    text-decoration: none;
    /* color: #f5f5f5; */
    color: #333;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }
  
  .nav-text a:hover {
    background-color: #1a83ff;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    /* background-color: #060b26; */
    background-color: whitesmoke;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
    
  }
  
  span {
    margin-left: 16px;
  }
  @media screen and (max-width:800px){
    .navbar {
        padding: 0 2rem 0 2rem;
      } 
  }